<template>
  <div v-if='pokemon'>
    <h1>Â¿Quien es ese Pokemon?</h1>
    <PokemonPicture :pokemonId='pokemon.id' :showPokemon='showPokemon'/>
    <PokemonOptions :pokemons='pokemonArr' @selection='checkAnswer'/> 
    
    <template v-if="showAnswer">
    <h2>{{ message }}</h2>
    <button @click="newGame">Nuevo Juego</button>
    </template>
  </div>
  <div v-else>
    <h1>Espere Por Favor......</h1>
  </div>
</template>

<script>
import PokemonOptions from '../components/PokemonOptions.vue'
import PokemonPicture from '../components/PokemonPicture.vue'
import getPokemonsOptions from './helpers/getPokemonsOptions' 

export default {
  components: { PokemonPicture, PokemonOptions },
  data(){
    return {
      pokemonArr: [],
      pokemon: null,
      showPokemon: false,
      showAnswer: false,
      message: ''
    }
  },
  methods:{
    async mixPokemonArray(){
      this.pokemonArr = await getPokemonsOptions()
      const pokeId    = Math.floor(Math.random() * 4) 
      this.pokemon    = this.pokemonArr[ pokeId ]
    },
    checkAnswer( pokemonId ){
      this.showPokemon = true
      this.showAnswer  = true
      this.message = ( pokemonId === this.pokemon.id )?
                      'Correcto!!!':
                      `Opps! era ${ this.pokemon.name }`

    },
    newGame(){
      this.showPokemon = false
      this.showAnswer  = false
      this.pokemonArr  = []
      this.mixPokemonArray()
    }

  },
  mounted(){
    this.mixPokemonArray()
  }
}
</script>

