import pokemonApi from '../../api/pokemonApi'

const randomId = () => {
    return Math.floor(Math.random() * 650) + 1
}
const getPokemonsOptions = async() => {
    let pokeArray = [];
    for( let n = 0; n < 5; n++ ){
        pokeArray[n] = randomId()
        for( let m = 0; m < 5; m++ ){
            pokeArray[n] = ( !(pokeArray[m] == pokeArray[n])) ? 
                            pokeArray[n] : 
                            randomId()    
        }
    }
    pokeArray = await getPokemonsNames( pokeArray )
    return pokeArray 
}
const getPokemonsNames = async( [ a, b, c, d , e]) => {
   
    const promiseArray = [
        pokemonApi.get(`/${ a }`),
        pokemonApi.get(`/${ b }`),
        pokemonApi.get(`/${ c }`),
        pokemonApi.get(`/${ d }`), 
        pokemonApi.get(`/${ e }`),     
    ]
    const [ p1, p2 , p3, p4, p5 ] = await Promise.all( promiseArray )
   return [
       { name : p1.data.name , id : p1.data.id },
       { name : p2.data.name , id : p2.data.id },
       { name : p3.data.name , id : p3.data.id },
       { name : p4.data.name , id : p4.data.id },
       { name : p5.data.name , id : p5.data.id },
   ]
}
export default getPokemonsOptions